import Vue from 'vue';
import VueI18n from 'vue-i18n';
import {changeLang} from "./utils";

Vue.use(VueI18n);



export default ({app, store, $cookies}) => {
  let locale = $cookies.get('Reward_locale') || store.state.locale
  // console.log(locale);
  changeLang(locale)
  app.i18n = new VueI18n({
    locale: locale,
    fallbackLocale: 'en-US',
    messages: {
      'en-US': require('~/locales/enUs.json'),
      'zh-CN': require('~/locales/zhCn.json')
    }
  });
  // app.i18n.path = (link) => {
  //   if (app.i18n.locale === app.i18n.fallbackLocale) {
  //     return `/${link}`;
  //   }
  //   return `/${app.i18n.locale}/${link}`;
  // }
}
