import enUS from 'vant/es/locale/lang/en-US';
import zhCN from 'vant/es/locale/lang/zh-CN';

import {Locale} from 'vant';

// Locale.use('zh-CN', zhCN);
// Locale.use('en-US', enUS);

Date.prototype.Format = function (fmt) {
  var o = {
    "M+": this.getMonth() + 1,                 //月份
    "d+": this.getDate(),                    //日
    "h+": this.getHours(),                   //小时
    "m+": this.getMinutes(),                 //分
    "s+": this.getSeconds(),                 //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
    "S": this.getMilliseconds()             //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

export function formatTimeToStr(times, pattern) {
  var d = new Date(times).Format("yyyy-MM-dd hh:mm:ss");
  if (pattern) {
    d = new Date(times).Format(pattern);
  }
  return d.toLocaleString();
}

export function changeLang(newlang) {
  // console.log(newlang);
  switch (newlang) {
    case "zh-CN":
      Locale.use('zh-CN', zhCN);
      break;
    case "en-US":
      Locale.use('en-US', enUS);
      break;
    default:
      Locale.use('en-US', enUS);
      break
  }
}

export function formatNera(nera, pattern = 2) {
  // let newNera = parseFloat(nera)
  // if (newNera === 0) return newNera
  // return newNera.toFixed(pattern)
  if (!nera) return  ''
  nera = nera.toString()
  let index = nera.indexOf('.')
  if (index !== -1) {
    nera = nera.substring(0, pattern + index + 1)
  } else {
    nera = nera.substring(0)
  }
  return parseFloat(nera).toFixed(pattern)
}


export function hideStrMid(str, len) {
  if (!str) return '';
  if (str.length < len / 2) return str;
  let temp = str.substring(0, parseInt(len / 2)) + '...' + str.substring(str.length - parseInt(len / 2));
  return temp;
}

export function clear_tags($str) {
  if (!$str) return '';
  var text1 = $str.replace(/<\/?.+?>/g, "");
  // return text1.replace(/ /g, "");;
  return text1;
}


