
import websocket from "@/plugins/websocket.js";
import axios from "axios";
//初始化websocket
export async function initWebSocket(uid){
    if(!uid)return;
    let res = await axios.post('v1/market/CreateListenKey',{sAddress: uid+''})
    res = res.data.data;
    if(!websocket.state.websock){
        websocket.mutations.INIT_WEBSOCKET(websocket.state,res.sListenKey);
    }
    else if (websocket.state.websock && websocket.state.websock.readyState === 1){
        websocket.mutations.PORTALSUBSCRIBE(res.sListenKey);
        // console.log('添加订阅主题----------------' + res.sListenKey);
    }
}
