import {getToken} from '~/plugins/auth'

export const state = () => ({
  // token: getToken(),
  locales: ['zh-CN', 'en-US'],
  locale: 'en-US',
  isLogin: false,
  isNeedLoad: true,
  nera: 0.1,
  userInfo: {},
  iRewardCount: '',
  iTrendCount: '',
  searchText: '',
  activityDetail: {},
})
export const mutations = {
  SET_ISNEEDLOAD(state, data) {
    state.isNeedLoad = data
  },
  SET_ISLOGIN(state, data) {
    state.isLogin = data
  },
  SET_USERINFO(state, data) {
    // data['sWallet'] = "0x1B1432B715638d520bd6C20e4fb5C8De65D8ae47"   // L00
    // data['sWallet'] = "0x13796eB5905C63618D7B7b53dEf41F2b520187F8"      // L01
    state.userInfo = data
  },
  SET_LANG(state, data) {
    if (state.locales.indexOf(data) !== -1) {
      state.locale = data
    }
  },
  SET_NERA(state, data) {
    state.neraPrice = data
  },
  CHANGE_DATA(state, data) {
    if ((data.key === 'iRewardCount' || data.key === 'iTrendCount') && !data.val) {
      data.val = ''
    }
    state[data.key] = data.val;

  },
  SET_SEARCHTEXT(state, data) {

  }
}
export const actions = {
  async UPDATE_USERINFO({commit}) {
    let res = await this.$axios.post('/v1/emall/GetRewardUserInfor', {})
    commit('SET_USERINFO', res)
  },
  async UPDATE_NERA({commit}) {
    let res = await this.$axios.post('/v1/market/GetSymbolTick', {vSymbol: ["nerausdt@miniTicker"]})
    let nera = res.vSymbolTick[0].c || 0.1
    commit('SET_NERA', nera)
  },
}
export const getters = {
  searchText: (state) => state.searchText
}
