import Vue from 'vue'
import {formatNera, hideStrMid} from "./utils";

Date.prototype.Format = function (fmt) {
  var o = {
    "M+": this.getMonth() + 1,                 //月份
    "d+": this.getDate(),                    //日
    "h+": this.getHours(),                   //小时
    "m+": this.getMinutes(),                 //分
    "s+": this.getSeconds(),                 //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
    "S": this.getMilliseconds()             //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

Vue.filter('formatNera', function (value, size = 2) {
  return formatNera(value, size)
})

Vue.filter('formatDate', function (times, pattern = 'yyyy-MM-dd') {
  let d = ''
  if (pattern) {
    d = new Date(times).Format(pattern);
  }
  return d.toLocaleString();
})

Vue.filter('filterHideMid', function (str, cutNum = 8) {
  if (!str) return '';
  if (str.length > cutNum) {
    return hideStrMid(str, cutNum);
  }
  return str
})

Vue.filter('filterNumFormat', function (num, digits) {
  var si = [
    {value: 1, symbol: ""},
    {value: 1E3, symbol: "k"},
    {value: 1E6, symbol: "M"},
    {value: 1E9, symbol: "G"},
    {value: 1E12, symbol: "T"},
    {value: 1E15, symbol: "P"},
  ];
  var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
  var i;
  for (i = si.length - 1; i > 0; i--) {
    if (num >= si[i].value) {
      break;
    }
  }
  return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;

})
