export default async function ({route, redirect, store, $cookies, $axios}) {
  const token = $cookies.get('Authorization', {parseJSON: false})
  const isNeedLoad = store.state.isNeedLoad  //是否需要重新加载
  // console.log($cookies);
  // console.log();
  const noLoginPage = [
    'Start',        // 首屏
    'Main',         // 首页
    'Billboard',    // 悬赏页 - index
    'BillboardList',   // 悬赏页
    'Nfts',   // Nfts市场
    'Detail',   // 详情页
    'Activity',   // 活动页
    'HelpCenter',   // 帮助中心
    'Share',   // 分享
  ]
  if (!(noLoginPage.includes(route.name))) {
    // 不在无需登录页面
    if (!token && !(route.path.includes('/account'))) {
      //  未登录 且 当前未在账号操作目录 跳转到登录页
      redirect('/account/login');
    }
  }
  if (token && isNeedLoad && !(route.path.includes('/account'))) {
    await store.dispatch('UPDATE_NERA', false)
    await store.dispatch('UPDATE_USERINFO', false)
    store.commit('SET_ISNEEDLOAD', false)
    store.commit('SET_ISLOGIN', true)
  }
}
