import {mapState} from 'vuex'
import {removeToken} from '@/plugins/auth'
import websocket from "@/plugins/websocket.js";
import {initWebSocket} from "@/plugins/initWebsocket";

export default {
  name: "iHeader",
  data() {
    return {
      showMenu: false,
      isSearchMode: false,
      searchText: '',
      activityRed: false,
      tabs: [
        {name: 'Nav01', title: 'ERAX Planet', icon: 'iconfont icon-planet', url: '/main'},
        {name: 'Nav02', title: 'Billboard', icon: 'iconfont icon-billboard', url: '/billboard'},
        {name: 'Nav03', title: 'Activity', icon: 'iconfont icon-activity', url: '/activity', border: true,},
        {name: 'Nav04', title: 'Message', icon: 'iconfont icon-message', url: '/account/message', isLogin: true},
        {name: 'Nav06', title: 'Login', icon: 'iconfont icon-user', url: '/account/login', isLogin: false},
        {name: 'Nav05', title: 'My Profile', icon: 'iconfont icon-user', url: '/user', isLogin: true},
        {name: 'Nav10', title: 'My Wallet', icon: 'iconfont icon-qianbao', url: '/account/nera', isLogin: true},
        {name: 'Nav09', title: 'Marketplace', icon: 'iconfont icon-nft', url: '/nft'},
        {name: 'Nav07', title: 'Help Center', icon: 'far fa-question-circle', url: '/help-center', border: true},
      ],
      locales: [
        {key: 'en-US', title: 'English'},
        {key: 'zh-CN', title: '简体中文'},
      ],
      selectLang: this.$cookies.get('Reward_locale') || this.locale
    }
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo,
      isLogin: (state) => state.isLogin,
      iRewardCount: (state) => state.iRewardCount,
      iTrendCount: (state) => state.iTrendCount,
      locale: (state) => state.locale,
    }),
    getMessageBadge() {
      let n1 = parseFloat(this.iRewardCount) ? parseFloat(this.iRewardCount) : 0;
      let n2 = parseFloat(this.iTrendCount) ? parseFloat(this.iTrendCount) : 0;
      let temp = n1 + n2;
      if (!temp) {
        temp = ''
      }
      return temp;
    }
    // isLogin() {
    //   console.log(this.userInfo);
    //   return !!this.userInfo.iUserId;
    // }
  },
  watch: {
    userInfo(v) {
      if (v.iUserId) {
        this.GetUserRewardMailUnreadCount();
      }
      return v
    },
    isLogin(v) {
      return v
    },
    locale(v) {
      this.selectLang = v
      return v
    },
  },
  mounted() {
    window.addEventListener("UPDATE-MAIL", this.GetUserRewardMailUnreadCount);

    //避免重复调用
    if (!websocket.state.websock && this.userInfo.iUserId) {
      initWebSocket(this.userInfo.iUserId);
    }
    this.GetUserRewardMailUnreadCount();
    this.getList();
  },
  methods: {
    async getList() {
      let {vActivity} = await this.$axios.post('v1/emall/GetActivityList', {iPageNo: 0, iPageNum: 100});
      let ary = [];
      let temp = this.$cookies.get('ActivityList');
      if (temp) {
        ary = temp;
        for (const iterator of vActivity) {
          if (ary.indexOf(iterator.iListId) > -1) {
            this.activityRed = true;
            return;
          }
        }
      } else {
        if (vActivity.length) {
          this.activityRed = true;
          return
        }
      }
      this.activityRed = false;
    },
    handleSetLang(key) {
      console.log(key);
      this.$i18n.locale = key;
      this.$store.commit('SET_LANG', key)
      // localStorage.setItem("Reward_locale", key);
      this.$cookies.set("Reward_locale", key)
      this.showMenu = false

    },
    async GetUserRewardMailUnreadCount() {
      if (!this.userInfo.iUserId) return;
      let {iRewardCount, iTrendCount} = await this.$axios.post('v1/udc/GetUserRewardMailUnreadCount', {});
      this.$store.commit('CHANGE_DATA', {key: 'iRewardCount', val: iRewardCount});
      this.$store.commit('CHANGE_DATA', {key: 'iTrendCount', val: iTrendCount});
    },
    handleLogout() {
      this.showMenu = false
      this.$store.commit('SET_USERINFO', {})
      this.$store.commit('SET_ISLOGIN', false)
      this.$store.commit('SET_ISNEEDLOAD', true)
      removeToken()
      if (this.$route.path !== '/main') {
        this.$router.replace('/main')
      }
    },
    goUrl(v) {
      this.showMenu = false
      // if (v.title === "Activity") {
      //   this.$toast('敬请期待')
      //   return
      // }
      this.$router.push(v.url)
    },
    handleBack() {
      switch (this.$route.name) {
        case 'Publish':
        case 'Answer':
          if (this.$store.state.reward.step === 1) {
            this.$store.commit('reward/SET_STEP', 0)
            break
          }
        default:
          this.$router.go(-1)
      }
    },
    handleMessage() {
      let evt = new CustomEvent('MESSAGE-READ');
      window.dispatchEvent(evt);
    },
    onSearch(val) {
      // console.log(val);

      this.$store.commit('SET_SEARCHTEXT', val)
    },
    onCancel() {
      this.isSearchMode = false
      this.$store.commit('SET_SEARCHTEXT', '')
    },
  }
}
