import Vue from 'vue'

export default function (app) {
  let axios = app.$axios;
  let i18n = app.app.i18n;

  const Authorization = app.$cookies.get('Authorization', {parseJSON: false})
  axios.defaults.withCredentials = true;
  // axios.defaults.timeout = 50000;
  axios.defaults.headers = {
    Authorization: Authorization || '',
    // Authorization: '',
  };
  axios.interceptors.request.use(
    config => {

      config['headers']['Authorization'] = app.$cookies.get('Authorization', {parseJSON: false}) || ''
      return config
    }
  )
  axios.interceptors.response.use(
    ({config, data}) => {
      // return data.data
      // console.log(data.iRspCode);
      switch (data.iRspCode) {
        case 0:
          return data.data
        case -509:
          // console.log(app);
          console.log(data.iRspCode);
          // Vue.prototype.$toast('登录超时');
          Vue.prototype.$toast(i18n.t(`ERR${data.iRspCode}`));

          app.redirect({name: 'Login'})
          break
        default:
          // this.$toast.error(vm.$i18n.t(`message.err${data.iRspCode}`));
          console.log(data.iRspCode);
          Vue.prototype.$toast(i18n.t(`ERR${data.iRspCode}`));
          return Promise.reject(data);
      }
    }
  )
}
